.main {
    display: block;
    width: 100%;
    background-color: var(--color-grey);
    height:100%;
    padding: 1rem;
    transition: var(--transition);
    margin-left: 19rem;
}

header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding: 0 1rem 1rem 1rem;
}

.search {
    display: flex;
    width: 100%;
    gap: 1rem;
}

.location, .business{
    width: 14rem;
}

.location, .business, button {
    display: flex;
    align-items: center;
    justify-content: space-around;
    border-radius: var(--border-radius-1);
    background-color: var(--color-white);
    cursor: pointer;
    padding: 0.5rem;
    border: 1px solid var(--color-btn-dark);
    transition: var(--transition);
}

.location:focus-within, .business:focus-within{
    box-shadow: 0 0 3px var(--color-btn-dark);
    width: 18rem;
}


.login-button {
    display: flex;
    gap: 1rem;
    font-size: 0.8rem;
  
}

.login-button .button{
    border-radius: var(--border-radius-2);
    display: flex;
    align-items: center;
    color: black;
    justify-content: center;
    background-color: var(--color-white);
    width: 6rem;
    height: 2.2rem;
    border: 1px solid var(--color-btn);
}

section {
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding: 0 1rem 1rem 1rem;
    gap: 2rem;
    
}

.section-grid {
    grid-template-columns: 1fr 1fr 1fr;
}

article {
    display: block;
    background-color: var(--color-white);
    border-radius: var(--border-radius-1);
    padding: 1rem 2rem 0 2rem;
}

article:hover{
    box-shadow: 0 1.3rem 1.3rem rgba(0, 0, 0, 0.1);
}

.location-status {
    display: flex;
    justify-content: space-between;
}

.location-status h2, .location-status p {
    font-size: 0.8rem;
    color: var(--color-btn);
}

.location-status p span {
    display: inline-block;
    width: 0.5rem;
    height: 0.5rem;
    border-radius: 100%;
    background-color:var(--color-active) ;
}
.location-status h2 span {
    font-weight: 600;
}

.banner-container {
    display: block;
}

.banner {
    width: 100%;
    height: 10rem;
    border-radius: var(--border-radius-1);
    margin-top: 0.5rem;
    overflow: hidden;
}

.banner-container p{
    display: block;
    padding: 1rem 0;
    font-size: 0.8rem;
}

.profile-container {
    display: flex;
    justify-content: space-between;
    border: 1px solid #ddd;
    border-radius: var(--border-radius-1);
    padding: 0.6rem;
}

.profile-container > div, .rate-container  {
    display: flex;
    line-height: 1rem;
    font-size: 0.75rem;
    gap: 0.3rem;
    align-items: center;
}

.profile-img{
    width: 2.7rem;
    height: 2.7rem;
    border-radius: 100%;
}

.profile p span{
    font-weight: 800;
}
.bookmark {
    cursor: var(--pointer);
}

.view{
    display: flex;
    gap: 1rem;
    align-items: center;
    border: 1.2px solid var(--color-btn);
    color: var(--color-btn);
    padding: 0.6rem;
    border-radius: var(--border-radius-2);
    font-weight: 800;
    text-align: center;
    cursor: var(--pointer);
    transition: var(--transition);
}

.view:hover{
    background-color: var(--color-btn);
    color: var(--color-white);
    border-radius: var(--border-radius-2);

}

.rate-container {
    justify-content: space-between;
}

.rate, .like {
    display: flex;
    gap: 0.1rem;
    color: var(--color-star);
    align-items: center;

}

.thumbs {
    cursor: var(--pointer);
}

.not-active {
    color: var(--color-btn-dark);
}

.like {
    gap: 1rem;
    padding: 0.8rem;
    font-size: 0.8rem;
    font-weight: 800;
    color: var(--color-btn);
}


