.aside{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: fixed;
    top: 0;
    left: 0;
    width: 20rem;
    height: 100vh;
    padding: 1rem 2rem;
    color: var(--color-dark);
    opacity: 1;
    transition: var(--transition);
    background-color: var(--color-white);
}

.aside-menu {
    display: block;
}

.menu-logo {
    display: flex;
    align-items: center;
    height: 4rem;
    gap: 1rem;
    margin-left: 1rem;
}

.logo {
    transition: var(--transition);
    width: 200px;
    height: 73px;
}

.burger {
    width: 24px;
    height: 24px;
}

.menu {
    font-size: 1.5rem;
    cursor:var(--pointer) ;
}

nav {
    margin-top: 1rem;
    color: var(--color-black); 
}

.aside-menu nav li .link, .link{
    display: flex;
    align-items: center;
    gap: 0.95rem;
    padding-left: 1rem;
    height: 3rem;
    transition: var(--transition);
    cursor: var(--pointer);
}


.aside-menu nav .link:hover, .link:hover, .active {
    color: var(--color-white);
    background-color: var(--color-btn);
}

.settings-container{
    display: block;
}

.verify {
    display: flex;
    flex-direction: column;
    background-color: var(--color-btn-dark);
    border-radius: var(--border-radius-1);
    padding:  1.5rem 2.5rem;
    transition-duration: 1s;
    color: var(--color-black);
    margin-top: 1rem;
    transition: var(--transition);
    overflow: hidden;

}

.settings-container .verify li{
    cursor: var(--pointer);
    display: flex;
    align-items: center;
    gap: 1rem;
}

.verify-img {
    width: 3.1rem;
    height: 3.1rem;
}
.hidden {
    display: none;
}
.show {
    display: flex;
}

.radius {
    border-radius: var(--border-radius-1);
    margin: 1rem 0;
}

.transition {
    overflow: hidden;
    transition: var(--transition);
}

.hidewidth {
    width: 0;
}
.scale{
    transition: var(--transition);
}

.hiddens {
    opacity: 0;
    transform: scaleX(0);
    width: 0;
    height: 0;
}

.aside.reduce {
    width: 7.2rem;
}
