/* ================ Sign Up syling ================ */

.sign-up {
    display: flex;
    position: fixed;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.7);
    text-align: center;
}

.sign-up-center {
    display: flex;
    justify-content: center;
    align-items:stretch;
    flex-direction: column;
    width: 26rem;
    height: 35rem;
    background-color: var(--color-white);
    border-radius: var(--border-radius-1);
    padding: 2rem 3rem;
    box-shadow: 0 3px 4px var(--color-black-transparent-2);
}

.size{
    width: 3.5rem;
    height: 3.5rem;
}

.logo-sign-up h1 {
    font-weight: bolder;
}


.sign-up-center p:first-child {
    font-style: normal;
    font-weight: 400;
    font-size: 0.73rem;
    margin-top: 0.4rem;
}

.mockup-img {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 0.4rem;
    margin: 1rem 0;
    
}

.m-1, .m-3, .m-2{
    display: flex;
    flex-direction: column;
    gap: 0.4rem;
    border-radius: var(--border-radius-1);
    overflow: hidden;
}

.sign-up-center .button, .Registration-link {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 0.8rem;
    height: 2.8rem;
    border-radius: var(--border-radius-1);
    border: 1.5px solid var(--color-btn);
    background-color: var(--color-btn-variant);
    color: var(--color-black);
}

.black {
    color: var(--color-btn);
}

.sign-up-center > p {
    padding: 0.4rem 0;
    font-size: 0.75rem;
}

.sign-up-center > p span{
    color: var(--color-btn);
    font-weight: 600;
    cursor: pointer;
}

.width {
    width: 6rem; 
    height: 6rem;
}

.width-2 {
    width: 96px;
    height: 198.39px;
}

.sign-up-center .home {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    color: black;
    top: 3rem;
    right: 3rem;
    width: 6.2rem;
    height: 2.7rem;
    font-size: 0.75rem;
    background-color: var(--color-white);
    border-radius: var(--border-radius-2);
}

p.very-small{
    font-size: 0.55rem;
}

p.very-small a {
    text-decoration: underline;
    color: var(--color-btn);
}

.transparent {
    background-color: transparent;
}

.hidden {
    display: none;
}

.show {
    display: flex;
}