.registration-container{
    display: flex;
    height: 100vh;
    align-items: center;
    justify-content: space-around;
}

.registration-img {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
}

.img {
    width: 30rem;
    height: 8.2rem;
}

.registration {
    display: flex;
    margin: auto;
}
.registration form {
    display: flex;
    text-align: center;
    flex-direction: column;
    justify-content: stretch;
    padding: 2rem;
    border-radius: var(--border-radius-1);
    gap: 0.5rem;
    width: 25rem;
}
.registration form h1{
    font-weight: bolder;
    font-size: 1.5rem;
}

.registration .steps{
    display: flex;
    flex-direction: column;
    font-size: 0.8rem;
}

.registration .next-step {
    display: flex;
    justify-content: space-between;
    position: relative;
}

.registration .next-step hr {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 0.3rem;
    width: 100%;
    background-color: var(--color-btn-dark);
    z-index: 1;
}

.registration .next-step span{
    display: inline-block;
    z-index: 2;
    background-color: var(--color-white);
    border: 0.12rem dashed var(--color-btn);
    width: 2.5rem;
    height: 2.5rem;
    border-radius: 100%;
    padding: 0.65rem;
    

 }

.registration .next-step-txt {
    display: flex;
    justify-content: space-between;
    padding: 0 0.1rem;
    margin: 0.8rem 0;  
}

.registration .next-step .active {
    background-color: var(--color-btn);
    color: var(--color-white);
}
.registration .next-step-txt .active{
    color: var(--color-btn);
}

.registration input {
    height: 2.8rem;
    border-radius: var(--border-radius-2);
    padding: 1rem;
    border: 1.9px solid var(--color-btn-dark);
    margin-bottom: 0.3rem;
}

.registration input:-webkit-autofill {
    box-shadow: 0 0 0 30px var(--color-btn-variant) inset !important;
}

.registration button {
    height: 2.8rem;
    border: 1.9px solid var(--color-btn);
    background-color: var(--color-btn-variant);
}