.explore-container, .setting-container, .local-container, .market-container {
    display: block;
    position: relative;
    width: 100%;
    margin-left: 19rem;
    background-color: var(--color-grey);
    height:95vh;
    padding: 1rem;
    transition: var(--transition);
}

.explore, .setting , .local, .market{
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: var(--color-btn);
    padding: 1rem;
    border-radius: var(--border-radius-1);
    color: var(--color-white);
}

.repair {
    width: 17rem;
    height: auto;
    margin-bottom: 1rem;
}
   

.slide-in {
    margin-left: 6.2rem;
    transition: var(--transition);
}
