:root{
    --color-bg:#fff;
    --color-bg-variant:#2c2c62;
    --color-star:#ee8a06;
    --color-white:#fff;
    --color-black:#000;
    --color-black-transparent-1: rgba(0, 0, 0, 0.7);
    --color-black-transparent-2: rgba(0, 0, 0, 0.5);
    --color-btn:#005c66;
    --color-btn-variant:#bcf7fd;
    --color-btn-dark:#ccc;
    --color-grey:#eee;
    --color-active:#0d0;
    --transition:all 0.1s;
    --border-radius-1:0.625rem;
    --border-radius-2:0.3rem;
    --pointer:pointer;
    --container-width-lg:100%;
    --container-width-md:86%;
    --container-width-sm:90%
}
    @font-face{
        font-family:"Poppins";
        src:url(./assets/fonts/Poppins-Regular.ttf)
    }
    
    *{
        font-family:"Poppins",Arial,sans-serif;
        margin:0;
        padding:0;
        border:0;
        outline:0;
        box-sizing:border-box;
        list-style:none;
        text-decoration:none;
        font-display: swap;
    }
    html{
        scroll-behavior:smooth
    }
    
    body{
        font-family:'Poppins',sans-serif;
        background-color:var(--color-bg);
        color:var(--color-black);
        font-size:.9rem
    }
    
    .container{
        width:var(--container-width-lg);
        display:flex;
        padding:1rem;
    }

    .login-container{
        display: flex;
        position:fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100vh;
        align-items: center;
        justify-content: center;
        text-align: center;
        background-color: var(--color-black-transparent-1);
    }

    .login {
        display: flex;
        justify-content: center;
        align-items:stretch;
        flex-direction: column;
        width: 26rem;
        height: 35rem;
        box-shadow: 0 3px 4px var(--color-black-transparent-2);
        background-color: var(--color-white);
        border-radius: var(--border-radius-1);
        padding: 2rem 3rem;
        gap: 0.3rem;
    }

    .login h1{
        font-weight:bolder;
    } 
    
    .login p{
        padding: 0.7rem 0;
        font-size: 0.8rem;
    }
    
    .login input {
        padding: 1rem;
        margin-bottom: 0.8rem;
        height: 3rem;
        border-radius: var(--border-radius-1);
        border: 1.8px solid var(--color-btn-dark);
    }

    .password-visibility {
        position: relative;
        display: flex;
        justify-content: left;
        background-color: var(--color-white);
        border: 1.5px solid var(--color-btn-dark);
        border-radius: var(--border-radius-1);
        margin-bottom: 0.8rem;
    }

    .login input[type='password'], .login input[type='text'] {
        border: 0;
        width: 90%;
        margin-bottom: 0;
    }
    
    .visible-icon {
        position: absolute;
        right: 1rem;
        top: 50%;
        transform: translateY(-50%);
        
    }
    
    .login input[type='submit'] {
        background-color: var(--color-btn-variant);
        border: 1.95px solid var(--color-btn);
        margin: 0;
        padding: 0;
        cursor: pointer;
    }

    .login .line {
        display: flex;
        align-items: center;
        justify-content: space-around;
        padding: 0;
        margin: 0;
        margin: 0.8rem 0;
    }
    
    .google {
        width: 1.2rem;
        height: 1.2rem;
    }

    .login div hr{
        height: 0.01rem;
        background-color: var(--color-btn-dark);
        display: inline-block;
        width: 30%;
    }
    
    .login > a{
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 0.7rem;
        background-color: var(--color-grey);
    }

    .login span{
       color: var(--color-btn);
       cursor: pointer;
       font-weight: bold;
    }

    .login .home {
        position: absolute;
        top: 3rem;
        right: 3rem;
        width: 6.2rem;
        height: 2.7rem;
        font-size: 0.75rem;
        color: black;
        background-color: var(--color-white);
        border-radius: var(--border-radius-2);
        border-color: var(--color-btn);
    }

    
   
    
    h1,h2,h3,h4,h5{
        font-weight:100
    }
    
    a{
        transition:var(--transition);
        color: var(--color-black);
    }
    
    p{
        color:#000
    }
    
    img{
        display:block;
        width:100%;
        background-position:top;
        object-fit:cover
    }
    
    button{
        display:inline-block;
        border-radius:0.3rem;
        background-color:var(--color-white);
        border:1px solid var(--color-bg-variant)
    }

    .size {
        margin: auto;
        width: 3.5rem;
        height: 3.5rem;
    }

  
    .display{
        display:block
    }

    .hidden{
        display: none;
    }

    .show{
        display:flex;
   }
   
   .slide-in {
    margin-left: 6.2rem;
    transition: var(--transition);
}
